import React from 'react';
import './style.css';

const CheckEmail = () => {
    const email = localStorage.getItem('email');
  return (
    <div className='checkEmail'>
        <h1 className='sent'> A Magic Link as been sent to: {email}</h1>
        <p className='directions'>Log into SmileLine by navigating to your email to click on the provided link!</p>
    </div>
  )
}

export default CheckEmail;